import { Store } from 'Components/Stores/IStore';
import { AccountsActions } from './AccountsActions';
import { IAccountsPage } from './IAccountsPage';
import { Grid, Button } from '@material-ui/core';
// import Typography from '@material-ui/core/Typography';

import { LoadingIndicator } from 'Components/UI';
import { Account } from './Components/Account';
import { GroupsAndUsers } from './Components/GroupsAndUsers';
import { SubHeader } from 'Components/UI/SubHeader';
import { SideMenu } from 'Components/UI/SideMenu';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';
import { accountsPageStyles } from './style';
// import { LoadingIndicator } from 'Components/UI';

//#endregion Interface Imports

export const AccountsPage: React.FC<IAccountsPage.IProps> = (props: IAccountsPage.IProps): JSX.Element => {
	const classes = accountsPageStyles();
	const [viewOnly, setViewOnly] = useState(true);
	const [loading, setLoading] = useState(false);
	const [activeItem, setActiveItem] = useState('Account');
	const [mode, setMode] = useState('view');
	const [isModalConfirmExitOpen, setIsModalConfirmExitOpen] = useState(false);
	const [isModalConfirmSaveOpen, setIsModalConfirmSaveOpen] = useState(false);
	console.log(setLoading, activeItem);
	console.log(props);



	const sideBarItems = [
		{
			name: 'Account',
			// label: 'POLICY'
		},
		{
			name: 'Activity Logs',
			// label: 'GROUPIE'
		},
		{
			name: 'Audit Logs',
			// label: 'GROUPIE'
		},
	];

	// const menuItems = [
	// 	{
	// 		name: 'Notifications',
	// 	},
	// 	{
	// 		name: 'System',
	// 	},
	// 	{
	// 		name: 'Password',
	// 	},
	// ]

	const breadCrumbs = [
		'ACCOUNT'
	];

	const editRole = () => {
		setViewOnly(false);
		setMode('edit');
		console.log(setActiveItem, 'yes edit');
	}

	const activeSideBarItem = (item) => {
		setActiveItem(item);
		setViewOnly(true);
	}

	const handleModalConfirmationClose = () => {
		setIsModalConfirmSaveOpen(false);
		setIsModalConfirmExitOpen(false);
	}


	// const activeMenuItems = (item) => {
	// 	console.log(item);
	// }

	return (
		<>
			<SubHeader
				headingTitle={'Brian Joseph M. Dimaculangan'}
				breadCrumbsArray={breadCrumbs}
				buttonFunc={editRole}
				buttonName={'Edit Account'}
				layoutType={'layout_1'}
				enableButton={true}
			// activeItems={activeMenuItems}
			// menuItemsArray={menuItems}
			></SubHeader>
			<div className={classes.main}>
				{loading ?
					(<LoadingIndicator />) : null
				}
				<Grid container xs={12}>
					<Grid item xs={3} className={classes.sideBarContainer}>
						<SideMenu
							items={sideBarItems}
							activeItems={activeSideBarItem}
						></SideMenu>
					</Grid>
					<Grid item xs={9} className={classes.contentContainer}>
						{
							activeItem == 'Account' && <Account mode={mode} classes={classes} viewOnly={viewOnly}></Account>
						}
						{
							activeItem == 'Accounts' && <GroupsAndUsers classes={classes} viewOnly={viewOnly}></GroupsAndUsers>
						}
					</Grid>
				</Grid>
			</div>
			<ModalComponent
				isModalOpen={isModalConfirmSaveOpen}
				onClose={handleModalConfirmationClose}
				message={'Save all changes made?'}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button onClick={() => { setIsModalConfirmSaveOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button className={classes.button} variant={'contained'} color={'primary'}>Save</Button>
						</Grid>
					</Grid>
				}
			/>
			<ModalComponent
				isModalOpen={isModalConfirmExitOpen}
				onClose={handleModalConfirmationClose}
				message={'Exit and remove all changes made?'}
				title={'Confirmation'}
				buttons={
					<Grid container justify="center" alignItems="center">
						<Grid item xs={6}>
							<Button onClick={() => { setIsModalConfirmExitOpen(false) }} className={classes.button} variant={'outlined'} color={'secondary'}>Cancel</Button>
						</Grid>
						<Grid item xs={6}>
							<Button className={classes.button} variant={'contained'} color={'primary'}>Continue</Button>
						</Grid>
					</Grid>
				}
			/>
		</>
	);
}

const mapStateToProps = (state: Store) => state.accounts;

const mapDispatchToProps = (dispatch: Dispatch) => ({
	Map: bindActionCreators(AccountsActions.Map, dispatch),
});

export { mapStateToProps, mapDispatchToProps };
